// src/pages/hr/interview-questions.js
import { useState, useEffect } from "react";
import { supabase } from "lib/supabase";
import HRSidebar from "@/layouts/hrSidebar";
import HRHeader from "@/layouts/hrHeader";
import { Toaster, toast } from "react-hot-toast";
import { Icon } from "@iconify/react";

import { EditorContent, useEditor } from "@tiptap/react";
import StarterKit from "@tiptap/starter-kit";

// Drag and Drop types
const ItemType = {
    QUESTION: "question",
};

// Draggable Question Component
const DraggableQuestion = ({ question, index, moveQuestion }) => {
    const [, drag] = useDrag({
        type: ItemType.QUESTION,
        item: { index },
    });

    const [{ isOver }, drop] = useDrop({
        accept: ItemType.QUESTION,
        hover(item) {
            if (item.index !== index) {
                moveQuestion(item.index, index);
                item.index = index; // Update the item's index to the new index
            }
        },
    });

    return (
        <tr
            ref={node => drag(drop(node))}
            className={`border-b hover:bg-opacity-80 transition duration-200 cursor-move ${isOver ? "bg-gray-200" : ""}`}
        >
            <td className="p-4 w-12">
                <Icon icon="mdi:drag" width={20} height={20} className="text-gray-500" />
            </td>
            <td className="p-4 text-sm">{question.id}</td>
            <td className="p-4 text-sm">
                <div
                    className="prose max-w-none line-clamp-2"
                    dangerouslySetInnerHTML={{ __html: question.text }}
                />
            </td>
            <td className="p-4 text-sm">{question.options.join(", ")}</td>
            <td className="p-4 text-sm">
                {question.points ? JSON.stringify(question.points) : "None"}
            </td>
            <td className="p-4 text-sm flex gap-2">
                <button
                    onClick={() => startEditing(question)}
                    className="px-3 py-1 bg-[#f05d23] text-white rounded-lg hover:bg-[#d94f1e] transition duration-200 flex items-center gap-2"
                >
                    <Icon icon="mdi:pencil" width={16} height={16} />
                    Edit
                </button>
                <button
                    onClick={() => handleDeleteQuestion(question.id, question.text)}
                    className="px-3 py-1 bg-red-500 text-white rounded-lg hover:bg-red-600 transition duration-200 flex items-center gap-2"
                >
                    <Icon icon="mdi:trash-can" width={16} height={16} />
                    Delete
                </button>
            </td>
        </tr>
    );
};

export default function HRInterviewQuestions({ mode = "light", toggleMode }) {
    const [isSidebarOpen, setIsSidebarOpen] = useState(false);
    const [questions, setQuestions] = useState([]);
    const [isAdding, setIsAdding] = useState(false);
    const [newQuestionText, setNewQuestionText] = useState("");
    const [newOptions, setNewOptions] = useState("");
    const [newPoints, setNewPoints] = useState("");
    const [editingQuestion, setEditingQuestion] = useState(null);
    const [searchQuery, setSearchQuery] = useState("");
    const [sortField, setSortField] = useState("order");
    const [sortDirection, setSortDirection] = useState("asc");
    const [filterPoints, setFilterPoints] = useState("all");

    const editor = useEditor({
        extensions: [StarterKit],
        content: newQuestionText,
        onUpdate: ({ editor }) => setNewQuestionText(editor.getHTML()),
        editorProps: {
            attributes: {
                class: `prose max-w-none p-4 border rounded-b-lg focus:outline-none focus:ring-2 focus:ring-[#f05d23] min-h-[100px] ${
                    mode === "dark" ? "bg-gray-700 border-gray-600 text-white" : "bg-white border-gray-300 text-[#231812]"
                }`,
            },
        },
    });

    useEffect(() => {
        fetchQuestions();
    }, [sortField, sortDirection]);

    const fetchQuestions = async () => {
        const { data, error } = await supabase
            .from("interview_questions")
            .select("*")
            .order(sortField, { ascending: sortDirection === "asc" });
        if (error) {
            console.error("Error fetching questions:", error);
            toast.error("Failed to load questions.");
        } else {
            setQuestions(data);
        }
    };

    const handleAddQuestion = async (e) => {
        e.preventDefault();
        const optionsArray = newOptions.split(",").map((opt) => opt.trim()).filter((opt) => opt);
        let pointsObject = {};
        try {
            pointsObject = newPoints ? JSON.parse(newPoints) : {};
        } catch (error) {
            toast.error("Invalid points format. Use JSON like: {\"Yes\": 10, \"No\": 0}");
            return;
        }

        if (!newQuestionText || optionsArray.length === 0) {
            toast.error("Please provide a question and at least one option.");
            return;
        }

        const { data, error } = await supabase
            .from("interview_questions")
            .insert([
                {
                    text: newQuestionText,
                    options: optionsArray,
                    points: pointsObject,
                    order: questions.length,
                },
            ])
            .select();

        if (error) {
            console.error("Error adding question:", error);
            toast.error("Failed to add question: " + error.message);
        } else {
            toast.success(`Question "${data[0].text}" added successfully!`, { icon: "✅" });
            setNewQuestionText("");
            setNewOptions("");
            setNewPoints("");
            editor?.commands.setContent("");
            setIsAdding(false);
            fetchQuestions();
        }
    };

    const handleEditQuestion = async (e) => {
        e.preventDefault();
        const optionsArray = newOptions.split(",").map((opt) => opt.trim()).filter((opt) => opt);
        let pointsObject = {};
        try {
            pointsObject = newPoints ? JSON.parse(newPoints) : {};
        } catch (error) {
            toast.error("Invalid points format. Use JSON like: {\"Yes\": 10, \"No\": 0}");
            return;
        }

        if (!newQuestionText || optionsArray.length === 0) {
            toast.error("Please provide a question and at least one option.");
            return;
        }

        const { data, error } = await supabase
            .from("interview_questions")
            .update({
                text: newQuestionText,
                options: optionsArray,
                points: pointsObject,
                updated_at: new Date().toISOString(),
            })
            .eq("id", editingQuestion.id)
            .select();

        if (error) {
            console.error("Error editing question:", error);
            toast.error("Failed to update question: " + error.message);
        } else {
            toast.success(`Question "${data[0].text}" updated successfully!`, { icon: "✅" });
            setEditingQuestion(null);
            setNewQuestionText("");
            setNewOptions("");
            setNewPoints("");
            editor?.commands.setContent("");
            fetchQuestions();
        }
    };

    const handleDeleteQuestion = async (id, text) => {
        const { error } = await supabase.from("interview_questions").delete().eq("id", id);
        if (error) {
            console.error("Error deleting question:", error);
            toast.error("Failed to delete question: " + error.message);
        } else {
            toast.success(`Question "${text}" deleted successfully!`, { icon: "🗑️" });
            fetchQuestions();
        }
    };

    const moveQuestion = (fromIndex, toIndex) => {
        const updatedQuestions = [...questions];
        const [movedQuestion] = updatedQuestions.splice(fromIndex, 1);
        updatedQuestions.splice(toIndex, 0, movedQuestion);
        setQuestions(updatedQuestions);
    };

    const handleSort = (field) => {
        if (sortField === field) {
            setSortDirection(sortDirection === "asc" ? "desc" : "asc");
        } else {
            setSortField(field);
            setSortDirection("asc");
        }
    };

    const filteredQuestions = questions
        .filter((q) => q.text.toLowerCase().includes(searchQuery.toLowerCase()))
        .filter((q) => {
            if (filterPoints === "hasPoints") return q.points && Object.keys(q.points).length > 0;
            if (filterPoints === "noPoints") return !q.points || Object.keys(q.points).length === 0;
            return true;
        });

    const startEditing = (question) => {
        setEditingQuestion(question);
        setNewQuestionText(question.text);
        setNewOptions(question.options.join(", "));
        setNewPoints(question.points ? JSON.stringify(question.points) : "");
        editor?.commands.setContent(question.text);
    };

    return (
        <DndProvider backend={HTML5Backend}>
            <div
                className={`min-h-screen flex flex-col ${
                    mode === "dark" ? "bg-gradient-to-b from-gray-900 to-gray-800" : "bg-gradient-to-b from-gray-50 to-gray-100"
                }`}
            >
                <Toaster position="top-right" reverseOrder={false} />
                <HRHeader mode={mode} toggleMode={toggleMode} onLogout={handleLogout} />
                <div className="flex flex-1">
                    <HRSidebar
                        isOpen={isSidebarOpen}
                        mode={mode}
                        onLogout={handleLogout}
                        toggleSidebar={() => setIsSidebarOpen(!isSidebarOpen)}
                    />
                    <div
                        className={`flex-1 p-6 transition-all duration-300 ${
                            isSidebarOpen ? "md:ml-[300px]" : "md:ml-[80px]"
                        }`}
                    >
                        <div className="max-w-5xl mx-auto">
                            <div className="flex justify-between items-center mb-6">
                                <div className="flex items-center gap-2">
                                    <Icon icon="mdi:comment-question-outline" className="w-6 h-6 text-[#f05d23]" />
                                    <h3 className={`text-xl font-bold ${mode === "dark" ? "text-white" : "text-[#231812]"}`}>
                                        Interview Questions
                                    </h3>
                                </div>
                                <button
                                    onClick={() => setIsAdding(true)}
                                    className="px-4 py-2 bg-[#f05d23] text-white rounded-lg hover:bg-[#d94f1e] flex items-center gap-2 transition duration-200 shadow-md"
                                >
                                    <Icon icon="mdi:plus" width={20} height={20} />
                                    Add Question
                                </button>
                            </div>

                            {/* Add/Edit Form */}
                            {(isAdding || editingQuestion) && (
                                <div
                                    className={`p-6 rounded-lg shadow-lg mb-8 border-t-4 border-[#f05d23] ${
                                        mode === "dark" ? "bg-gray-800" : "bg-white"
                                    }`}
                                >
                                    <h4 className={`text-lg font-semibold mb-4 ${mode === "dark" ? "text-white" : "text-[#231812]"}`}>
                                        {editingQuestion ? "Edit Question" : "Add New Question"}
                                    </h4>
                                    <form onSubmit={editingQuestion ? handleEditQuestion : handleAddQuestion} className="space-y-6">
                                        <div>
                                            <label
                                                className={`block text-sm font-medium mb-2 ${
                                                    mode === "dark" ? "text-gray-300" : "text-[#231812]"
                                                }`}
                                            >
                                                Question Text <span className="text-red-500">*</span>
                                            </label>
                                            <div
                                                className={`border rounded-lg overflow-hidden ${
                                                    mode === "dark" ? "border-gray-600" : "border-gray-300"
                                                }`}
                                            >
                                                <EditorContent editor={editor} />
                                            </div>
                                        </div>
                                        <div>
                                            <label
                                                className={`block text-sm font-medium mb-2 ${
                                                    mode === "dark" ? "text-gray-300" : "text-[#231812]"
                                                }`}
                                            >
                                                Options (comma-separated) <span className="text-red-500">*</span>
                                            </label>
                                            <input
                                                type="text"
                                                value={newOptions}
                                                onChange={(e) => setNewOptions(e.target.value)}
                                                className={`w-full p-3 border rounded-lg focus:outline-none focus:ring-2 focus:ring-[#f05d23] transition duration-200 ${
                                                    mode === "dark"
                                                        ? "bg-gray-700 border-gray-600 text-white"
                                                        : "bg-gray-50 border-gray-300 text-[#231812]"
                                                }`}
                                                placeholder="e.g., Yes, No"
                                                required
                                            />
                                        </div>
                                        <div>
                                            <label
                                                className={`block text-sm font-medium mb-2 ${
                                                    mode === "dark" ? "text-gray-300" : "text-[#231812]"
                                                }`}
                                            >
                                                Points (JSON, optional) <span className="text-gray-500">e.g., {"{\"Yes\": 10, \"No\": 0}"}</span>
                                            </label>
                                            <input
                                                type="text"
                                                value={newPoints}
                                                onChange={(e) => setNewPoints(e.target.value)}
                                                className={`w-full p-3 border rounded-lg focus:outline-none focus:ring-2 focus:ring-[#f05d23] transition duration-200 ${
                                                    mode === "dark"
                                                        ? "bg-gray-700 border-gray-600 text-white"
                                                        : "bg-gray-50 border-gray-300 text-[#231812]"
                                                }`}
                                                placeholder='e.g., {"Yes": 10, "No": 0}'
                                            />
                                        </div>
                                        <div className="flex gap-4">
                                            <button
                                                type="submit"
                                                className="flex-1 py-2 bg-[#f05d23] text-white rounded-lg hover:bg-[#d94f1e] transition duration-200 shadow-md flex items-center justify-center gap-2"
                                            >
                                                <Icon icon={editingQuestion ? "mdi:pencil" : "mdi:plus"} width={20} height={20} />
                                                {editingQuestion ? "Update Question" : "Add Question"}
                                            </button>
                                            <button
                                                type="button"
                                                onClick={() => {
                                                    setIsAdding(false);
                                                    setEditingQuestion(null);
                                                    setNewQuestionText("");
                                                    setNewOptions("");
                                                    setNewPoints("");
                                                    editor?.commands.setContent("");
                                                }}
                                                className="flex-1 py-2 bg-gray-500 text-white rounded-lg hover:bg-gray-600 transition duration-200 shadow-md flex items-center justify-center gap-2"
                                            >
                                                <Icon icon="mdi:close" width={20} height={20} />
                                                Cancel
                                            </button>
                                        </div>
                                    </form>
                                </div>
                            )}

                            {/* Search and Filter */}
                            <div className="mb-6 flex flex-col md:flex-row gap-4">
                                <div className="flex-1 relative">
                                    <Icon
                                        icon="mdi:magnify"
                                        className="absolute left-3 top-1/2 transform -translate-y-1/2 w-5 h-5 text-[#f05d23]"
                                    />
                                    <input
                                        type="text"
                                        value={searchQuery}
                                        onChange={(e) => setSearchQuery(e.target.value)}
                                        className={`w-full pl-10 pr-4 py-2 border rounded-lg focus:outline-none focus:ring-2 focus:ring-[#f05d23] transition duration-200 ${
                                            mode === "dark"
                                                ? "bg-gray-700 border-gray-600 text-white"
                                                : "bg-gray-50 border-gray-300 text-[#231812]"
                                        }`}
                                        placeholder="Search questions..."
                                    />
                                </div>
                                <div className="flex items-center gap-2">
                                    <label
                                        className={`text-sm font-medium ${
                                            mode === "dark" ? "text-gray-300" : "text-[#231812]"
                                        }`}
                                    >
                                        Filter by Points:
                                    </label>
                                    <select
                                        value={filterPoints}
                                        onChange={(e) => setFilterPoints(e.target.value)}
                                        className={`p-2 border rounded-lg focus:outline-none focus:ring-2 focus:ring-[#f05d23] transition duration-200 ${
                                            mode === "dark"
                                                ? "bg-gray-700 border-gray-600 text-white"
                                                : "bg-gray-50 border-gray-300 text-[#231812]"
                                        }`}
                                    >
                                        <option value="all">All</option>
                                        <option value="hasPoints">Has Points</option>
                                        <option value="noPoints">No Points</option>
                                    </select>
                                </div>
                            </div>

                            {/* Questions Table */}
                            <div
                                className={`rounded-lg shadow-lg overflow-hidden ${
                                    mode === "dark" ? "bg-gray-800" : "bg-white"
                                }`}
                            >
                                <div className="max-h-[500px] overflow-y-auto">
                                    <table className="w-full">
                                        <thead className="sticky top-0 z-10">
                                        <tr className={`${mode === "dark" ? "bg-gray-700" : "bg-gray-100"}`}>
                                            <th className="p-4 w-12"></th>
                                            <th
                                                className="p-4 text-left text-sm font-semibold cursor-pointer"
                                                onClick={() => handleSort("id")}
                                            >
                                                ID{" "}
                                                {sortField === "id" && (
                                                    <Icon
                                                        icon={sortDirection === "asc" ? "mdi:arrow-up" : "mdi:arrow-down"}
                                                        width={16}
                                                        height={16}
                                                        className="inline"
                                                    />
                                                )}
                                            </th>
                                            <th
                                                className="p-4 text-left text-sm font-semibold cursor-pointer"
                                                onClick={() => handleSort("text")}
                                            >
                                                Question{" "}
                                                {sortField === "text" && (
                                                    <Icon
                                                        icon={sortDirection === "asc" ? "mdi:arrow-up" : "mdi:arrow-down"}
                                                        width={16}
                                                        height={16}
                                                        className="inline"
                                                    />
                                                )}
                                            </th>
                                            <th className="p-4 text-left text-sm font-semibold">Options</th>
                                            <th className="p-4 text-left text-sm font-semibold">Points</th>
                                            <th className="p-4 text-left text-sm font-semibold">Actions</th>
                                        </tr>
                                        </thead>
                                        <tbody>
                                        {filteredQuestions.map((question, index) => (
                                            <DraggableQuestion
                                                key={question.id}
                                                question={question}
                                                index={index}
                                                moveQuestion={moveQuestion}
                                            />
                                        ))}
                                        </tbody>
                                    </table>
                                    {filteredQuestions.length === 0 && (
                                        <p
                                            className={`text-center p-4 italic ${
                                                mode === "dark" ? "text-gray-400" : "text-gray-500"
                                            }`}
                                        >
                                            No questions match your search or filter.
                                        </p>
                                    )}
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <footer
                    className={`p-4 text-center text-sm shadow-inner ${
                        mode === "dark" ? "bg-gray-900 text-gray-400" : "bg-white text-gray-500"
                    }`}
                >
                    © {new Date().getFullYear()} Growthpad Consulting Group. All rights reserved.
                </footer>
            </div>
        </DndProvider>
    );
}